'use client'

import { useState } from 'react'
import Link from 'next/link'

// Tab Configuration - Shared across Contacts module
const CONTACT_TABS = [
  { id: 'contacts', label: 'Contacts', href: '/sub-account/contacts' },
  { id: 'bulk-actions', label: 'Bulk Actions', href: '/sub-account/contacts/bulk-actions' },
  { id: 'restore', label: 'Restore', href: '/sub-account/contacts/restore' },
  { id: 'tasks', label: 'Tasks', href: '/sub-account/contacts/tasks' },
  { id: 'companies', label: 'Companies', href: '/sub-account/contacts/companies' },
  { id: 'smart-lists', label: 'Smart Lists', href: '/sub-account/contacts/smart-lists' },
  { id: 'settings', label: 'Settings', href: '/sub-account/contacts/settings' },
]

// Types
interface DeletedContact {
  id: string
  firstName: string
  lastName: string
  email: string
  phone?: string
  company?: string
  status: string
  tags: string[]
  deletedAt: string
  deletedBy: string
  deleteReason?: string
  actionId?: string // If deleted via bulk action
  expiresAt: string // When permanently deleted
}

// Mock data for deleted contacts
const mockDeletedContacts: DeletedContact[] = [
  {
    id: 'del-001',
    firstName: 'Michael',
    lastName: 'Johnson',
    email: 'michael.johnson@example.com',
    phone: '+1 555-0101',
    company: 'TechCorp',
    status: 'customer',
    tags: ['VIP', 'Enterprise'],
    deletedAt: '2024-12-18T09:30:00Z',
    deletedBy: 'John Smith',
    deleteReason: 'Duplicate entry',
    expiresAt: '2025-01-17T09:30:00Z',
  },
  {
    id: 'del-002',
    firstName: 'Sarah',
    lastName: 'Williams',
    email: 'sarah.w@startup.io',
    phone: '+1 555-0102',
    company: 'Startup.io',
    status: 'qualified',
    tags: ['Startup', 'SaaS'],
    deletedAt: '2024-12-17T14:00:00Z',
    deletedBy: 'Sarah Johnson',
    deleteReason: 'Customer requested removal',
    expiresAt: '2025-01-16T14:00:00Z',
  },
  {
    id: 'del-003',
    firstName: 'Robert',
    lastName: 'Davis',
    email: 'robert.davis@enterprise.com',
    phone: '+1 555-0103',
    company: 'Enterprise Solutions',
    status: 'lost',
    tags: ['Enterprise'],
    deletedAt: '2024-12-16T11:00:00Z',
    deletedBy: 'Bulk Action',
    actionId: 'ba-005',
    expiresAt: '2025-01-15T11:00:00Z',
  },
  {
    id: 'del-004',
    firstName: 'Emily',
    lastName: 'Brown',
    email: 'emily.brown@agency.co',
    phone: '+1 555-0104',
    company: 'Creative Agency',
    status: 'new',
    tags: ['Agency', 'Marketing'],
    deletedAt: '2024-12-15T16:30:00Z',
    deletedBy: 'Mike Chen',
    expiresAt: '2025-01-14T16:30:00Z',
  },
  {
    id: 'del-005',
    firstName: 'David',
    lastName: 'Miller',
    email: 'dmiller@consulting.biz',
    company: 'Miller Consulting',
    status: 'contacted',
    tags: ['Consulting', 'B2B'],
    deletedAt: '2024-12-14T10:00:00Z',
    deletedBy: 'Admin',
    deleteReason: 'Inactive for 2+ years',
    expiresAt: '2025-01-13T10:00:00Z',
  },
]

export default function RestorePage() {
  const [deletedContacts, setDeletedContacts] = useState<DeletedContact[]>(mockDeletedContacts)
  const [selectedContacts, setSelectedContacts] = useState<Set<string>>(new Set())
  const [search, setSearch] = useState('')
  const [restoring, setRestoring] = useState<string | null>(null)
  const [showBulkRestoreModal, setShowBulkRestoreModal] = useState(false)
  const [showPermanentDeleteModal, setShowPermanentDeleteModal] = useState(false)
  const [contactToDelete, setContactToDelete] = useState<DeletedContact | null>(null)

  // Filter contacts
  const filteredContacts = deletedContacts.filter(contact => {
    const searchLower = search.toLowerCase()
    return (
      contact.firstName.toLowerCase().includes(searchLower) ||
      contact.lastName.toLowerCase().includes(searchLower) ||
      contact.email.toLowerCase().includes(searchLower) ||
      contact.company?.toLowerCase().includes(searchLower)
    )
  })

  // Toggle contact selection
  const toggleSelection = (id: string) => {
    const newSelection = new Set(selectedContacts)
    if (newSelection.has(id)) {
      newSelection.delete(id)
    } else {
      newSelection.add(id)
    }
    setSelectedContacts(newSelection)
  }

  // Select all visible contacts
  const selectAll = () => {
    if (selectedContacts.size === filteredContacts.length) {
      setSelectedContacts(new Set())
    } else {
      setSelectedContacts(new Set(filteredContacts.map(c => c.id)))
    }
  }

  // Restore single contact
  const restoreContact = async (contact: DeletedContact) => {
    setRestoring(contact.id)
    // Simulate API call
    await new Promise(resolve => setTimeout(resolve, 1000))
    setDeletedContacts(deletedContacts.filter(c => c.id !== contact.id))
    setRestoring(null)
  }

  // Bulk restore
  const bulkRestore = async () => {
    setShowBulkRestoreModal(false)
    // Simulate API call
    await new Promise(resolve => setTimeout(resolve, 1500))
    setDeletedContacts(deletedContacts.filter(c => !selectedContacts.has(c.id)))
    setSelectedContacts(new Set())
  }

  // Permanent delete
  const permanentDelete = async () => {
    if (!contactToDelete) return
    // Simulate API call
    await new Promise(resolve => setTimeout(resolve, 500))
    setDeletedContacts(deletedContacts.filter(c => c.id !== contactToDelete.id))
    setContactToDelete(null)
    setShowPermanentDeleteModal(false)
  }

  // Format date
  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleString('en-US', {
      month: 'short',
      day: 'numeric',
      year: 'numeric',
      hour: '2-digit',
      minute: '2-digit',
    })
  }

  // Days until permanent deletion
  const daysUntilExpiry = (expiresAt: string) => {
    const now = new Date()
    const expiry = new Date(expiresAt)
    const diff = expiry.getTime() - now.getTime()
    return Math.ceil(diff / (1000 * 60 * 60 * 24))
  }

  // Get avatar gradient
  const getAvatarGradient = (index: number) => {
    const gradients = [
      'from-violet-500 to-purple-500',
      'from-cyan-500 to-blue-500',
      'from-emerald-500 to-teal-500',
      'from-amber-500 to-orange-500',
      'from-rose-500 to-pink-500',
    ]
    return gradients[index % gradients.length]
  }

  return (
    <div className="space-y-6 pb-8">
      {/* Page Header with Tabs */}
      <div className="flex-none bg-white border-b border-slate-200 -mx-6 -mt-6 px-6 pt-6 pb-0 mb-6 rounded-t-2xl">
        <div className="flex items-center justify-between mb-4">
          <div>
            <h1 className="text-3xl font-bold bg-gradient-to-r from-violet-600 to-purple-600 bg-clip-text text-transparent">
              Contacts
            </h1>
            <p className="text-slate-600 mt-1">
              Recover deleted contacts before they're permanently removed
            </p>
          </div>
          {selectedContacts.size > 0 && (
            <button
              onClick={() => setShowBulkRestoreModal(true)}
              className="px-5 py-2.5 bg-gradient-to-r from-emerald-600 to-teal-600 text-white rounded-xl font-medium hover:from-emerald-700 hover:to-teal-700 transition-all shadow-lg shadow-emerald-500/30 flex items-center gap-2 justify-center"
            >
              <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
              </svg>
              Restore Selected ({selectedContacts.size})
            </button>
          )}
        </div>
        
        {/* Tab Navigation */}
        <div className="flex items-center gap-1 overflow-x-auto pb-0 -mb-px">
          {CONTACT_TABS.map(tab => (
            <Link
              key={tab.id}
              href={tab.href}
              className={`px-4 py-3 text-sm font-medium border-b-2 transition-colors whitespace-nowrap ${
                tab.id === 'restore'
                  ? 'border-violet-600 text-violet-600'
                  : 'border-transparent text-slate-500 hover:text-slate-700 hover:border-slate-300'
              }`}
            >
              {tab.label}
            </Link>
          ))}
        </div>
      </div>

      {/* Info Banner */}
      <div className="bg-amber-50 border border-amber-200 rounded-2xl p-4 flex items-start gap-3">
        <div className="w-10 h-10 bg-amber-100 rounded-xl flex items-center justify-center flex-shrink-0">
          <svg className="w-5 h-5 text-amber-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
          </svg>
        </div>
        <div>
          <h3 className="font-medium text-amber-900">Retention Policy</h3>
          <p className="text-sm text-amber-700">
            Deleted contacts are kept for 30 days before permanent deletion. After this period, they cannot be recovered.
          </p>
        </div>
      </div>

      {/* Search and Stats */}
      <div className="bg-white rounded-2xl shadow-sm border border-slate-200 p-4 sm:p-6">
        <div className="flex flex-col sm:flex-row gap-4 items-start sm:items-center justify-between">
          <div className="flex-1 relative max-w-md">
            <svg className="absolute left-4 top-1/2 transform -translate-y-1/2 w-5 h-5 text-slate-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
            </svg>
            <input
              type="text"
              value={search}
              onChange={(e) => setSearch(e.target.value)}
              placeholder="Search deleted contacts..."
              className="w-full pl-12 pr-4 py-3 border border-slate-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-violet-500 focus:border-transparent"
            />
          </div>
          <div className="flex items-center gap-4 text-sm">
            <span className="text-slate-600">
              <span className="font-semibold text-slate-900">{deletedContacts.length}</span> contacts in trash
            </span>
          </div>
        </div>
      </div>

      {/* Contacts List */}
      {filteredContacts.length === 0 ? (
        <div className="bg-white rounded-2xl shadow-sm border border-slate-200 p-12 text-center">
          <div className="w-16 h-16 bg-emerald-100 rounded-2xl flex items-center justify-center mx-auto mb-4">
            <svg className="w-8 h-8 text-emerald-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
            </svg>
          </div>
          <h3 className="text-lg font-semibold text-slate-900 mb-2">No deleted contacts</h3>
          <p className="text-slate-600">
            {search ? 'No contacts match your search' : 'Your trash is empty'}
          </p>
        </div>
      ) : (
        <div className="bg-white rounded-2xl shadow-sm border border-slate-200 overflow-hidden">
          {/* Select All Header */}
          <div className="px-6 py-4 bg-slate-50 border-b border-slate-200 flex items-center justify-between">
            <label className="flex items-center gap-3 cursor-pointer">
              <input
                type="checkbox"
                checked={selectedContacts.size === filteredContacts.length && filteredContacts.length > 0}
                onChange={selectAll}
                className="w-5 h-5 rounded-md border-slate-300 text-violet-600 focus:ring-violet-500"
              />
              <span className="text-sm font-medium text-slate-700">
                Select All ({filteredContacts.length})
              </span>
            </label>
            {selectedContacts.size > 0 && (
              <span className="text-sm text-violet-600 font-medium">
                {selectedContacts.size} selected
              </span>
            )}
          </div>

          {/* Contacts */}
          <div className="divide-y divide-slate-200">
            {filteredContacts.map((contact, index) => {
              const expiryDays = daysUntilExpiry(contact.expiresAt)
              const isUrgent = expiryDays <= 7

              return (
                <div
                  key={contact.id}
                  className={`p-5 hover:bg-slate-50 transition-colors ${
                    selectedContacts.has(contact.id) ? 'bg-violet-50' : ''
                  }`}
                >
                  <div className="flex items-start gap-4">
                    {/* Checkbox */}
                    <input
                      type="checkbox"
                      checked={selectedContacts.has(contact.id)}
                      onChange={() => toggleSelection(contact.id)}
                      className="mt-1 w-5 h-5 rounded-md border-slate-300 text-violet-600 focus:ring-violet-500"
                    />

                    {/* Avatar */}
                    <div className={`w-12 h-12 rounded-xl bg-gradient-to-br ${getAvatarGradient(index)} flex items-center justify-center text-white font-bold flex-shrink-0 opacity-60`}>
                      {contact.firstName[0]}{contact.lastName[0]}
                    </div>

                    {/* Contact Info */}
                    <div className="flex-1 min-w-0">
                      <div className="flex items-start justify-between gap-4">
                        <div>
                          <h3 className="font-semibold text-slate-900 mb-1">
                            {contact.firstName} {contact.lastName}
                          </h3>
                          <p className="text-sm text-slate-600">{contact.email}</p>
                          {contact.company && (
                            <p className="text-sm text-slate-500">{contact.company}</p>
                          )}
                        </div>
                        <div className="text-right">
                          <div className={`px-3 py-1 rounded-lg text-xs font-semibold ${
                            isUrgent 
                              ? 'bg-red-100 text-red-700' 
                              : 'bg-slate-100 text-slate-600'
                          }`}>
                            {expiryDays} days left
                          </div>
                        </div>
                      </div>

                      {/* Meta Info */}
                      <div className="mt-3 flex flex-wrap items-center gap-3 text-xs text-slate-500">
                        <span className="flex items-center gap-1">
                          <svg className="w-3.5 h-3.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                          </svg>
                          Deleted {formatDate(contact.deletedAt)}
                        </span>
                        <span className="flex items-center gap-1">
                          <svg className="w-3.5 h-3.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
                          </svg>
                          by {contact.deletedBy}
                        </span>
                        {contact.deleteReason && (
                          <span className="flex items-center gap-1">
                            <svg className="w-3.5 h-3.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 16h-1v-4h-1m1-4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
                            </svg>
                            {contact.deleteReason}
                          </span>
                        )}
                      </div>

                      {/* Tags */}
                      {contact.tags.length > 0 && (
                        <div className="mt-2 flex flex-wrap gap-1.5">
                          {contact.tags.map((tag, i) => (
                            <span key={i} className="px-2 py-0.5 bg-slate-100 text-slate-600 rounded text-xs">
                              {tag}
                            </span>
                          ))}
                        </div>
                      )}
                    </div>

                    {/* Actions */}
                    <div className="flex items-center gap-2 flex-shrink-0">
                      <button
                        onClick={() => restoreContact(contact)}
                        disabled={restoring === contact.id}
                        className="px-4 py-2 bg-emerald-600 text-white rounded-xl font-medium text-sm hover:bg-emerald-700 transition-colors disabled:opacity-50 disabled:cursor-not-allowed flex items-center gap-2"
                      >
                        {restoring === contact.id ? (
                          <>
                            <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin" />
                            Restoring...
                          </>
                        ) : (
                          <>
                            <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
                            </svg>
                            Restore
                          </>
                        )}
                      </button>
                      <button
                        onClick={() => {
                          setContactToDelete(contact)
                          setShowPermanentDeleteModal(true)
                        }}
                        className="px-4 py-2 border border-red-300 text-red-600 rounded-xl font-medium text-sm hover:bg-red-50 transition-colors"
                        title="Delete permanently"
                      >
                        <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                        </svg>
                      </button>
                    </div>
                  </div>
                </div>
              )
            })}
          </div>
        </div>
      )}

      {/* Bulk Restore Modal */}
      {showBulkRestoreModal && (
        <div className="fixed inset-0 bg-black/50 backdrop-blur-sm flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-2xl max-w-md w-full shadow-2xl overflow-hidden">
            <div className="bg-gradient-to-r from-emerald-600 to-teal-600 px-6 py-4 flex items-center justify-between">
              <div className="flex items-center gap-3">
                <div className="w-10 h-10 bg-white/20 rounded-xl flex items-center justify-center backdrop-blur-sm">
                  <svg className="w-5 h-5 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
                  </svg>
                </div>
                <div>
                  <h3 className="text-lg font-semibold text-white">Restore Contacts</h3>
                  <p className="text-sm text-emerald-100">{selectedContacts.size} contacts selected</p>
                </div>
              </div>
              <button
                onClick={() => setShowBulkRestoreModal(false)}
                className="text-white hover:bg-white/20 rounded-lg p-2 transition-colors"
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>

            <div className="p-6">
              <p className="text-slate-700 mb-6">
                Are you sure you want to restore <span className="font-semibold">{selectedContacts.size} contacts</span>? 
                They will be returned to your active contacts list.
              </p>

              <div className="flex gap-3">
                <button
                  onClick={() => setShowBulkRestoreModal(false)}
                  className="flex-1 px-4 py-3 border border-slate-300 text-slate-700 rounded-xl hover:bg-slate-50 font-medium"
                >
                  Cancel
                </button>
                <button
                  onClick={bulkRestore}
                  className="flex-1 px-4 py-3 bg-gradient-to-r from-emerald-600 to-teal-600 text-white rounded-xl font-medium hover:from-emerald-700 hover:to-teal-700 transition-all shadow-lg"
                >
                  Restore All
                </button>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Permanent Delete Modal */}
      {showPermanentDeleteModal && contactToDelete && (
        <div className="fixed inset-0 bg-black/50 backdrop-blur-sm flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-2xl max-w-md w-full shadow-2xl overflow-hidden">
            <div className="bg-gradient-to-r from-red-600 to-rose-600 px-6 py-4 flex items-center justify-between">
              <div className="flex items-center gap-3">
                <div className="w-10 h-10 bg-white/20 rounded-xl flex items-center justify-center backdrop-blur-sm">
                  <svg className="w-5 h-5 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z" />
                  </svg>
                </div>
                <div>
                  <h3 className="text-lg font-semibold text-white">Permanent Deletion</h3>
                  <p className="text-sm text-red-100">This cannot be undone</p>
                </div>
              </div>
              <button
                onClick={() => {
                  setShowPermanentDeleteModal(false)
                  setContactToDelete(null)
                }}
                className="text-white hover:bg-white/20 rounded-lg p-2 transition-colors"
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>

            <div className="p-6">
              <p className="text-slate-700 mb-4">
                Are you sure you want to permanently delete{' '}
                <span className="font-semibold">{contactToDelete.firstName} {contactToDelete.lastName}</span>?
              </p>
              <div className="bg-red-50 border border-red-200 rounded-xl p-4 mb-6">
                <p className="text-sm text-red-700">
                  <strong>Warning:</strong> This action is irreversible. All data associated with this contact will be permanently removed.
                </p>
              </div>

              <div className="flex gap-3">
                <button
                  onClick={() => {
                    setShowPermanentDeleteModal(false)
                    setContactToDelete(null)
                  }}
                  className="flex-1 px-4 py-3 border border-slate-300 text-slate-700 rounded-xl hover:bg-slate-50 font-medium"
                >
                  Cancel
                </button>
                <button
                  onClick={permanentDelete}
                  className="flex-1 px-4 py-3 bg-gradient-to-r from-red-600 to-rose-600 text-white rounded-xl font-medium hover:from-red-700 hover:to-rose-700 transition-all shadow-lg"
                >
                  Delete Permanently
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
